import { useContext, useState } from 'react'
import Modal from 'react-modal';
import { allowCharacterNumberInput, allowNumberInput, nullToNA } from '../../../Components/Common/PowerUps/PowerupFunctions';
import { useFormik } from "formik";
import * as yup from "yup";
import CitizenApplyApiList from '../../../Components/CitizenApplyApiList';
import BarLoader from '../../../Components/Common/BarLoader';
import axios from 'axios';
import { contextVar } from '../../../Components/ContextVar';
import { useNavigate } from 'react-router-dom';
import { allowCharacterNumberForwardInput } from '../../AdvertisementModule/FunctionComponent/ValidationFunction';
import ApiHeader from '../../../Components/ApiList/ApiHeader';
import CommonModal from '../../../Components/GlobalData/CommonModal';
import ServerErrorCard from '../../AdvertisementModule/ServerErrorCard';
import BottomErrorCard from '../../../Components/BottomErrorCard';
import ListTable from '../../../Components/Common/ListTable/ListTable';


const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        backgroundColor: 'white',
        border: 'none'
    },
};
Modal.setAppElement('#root');
function PtnNoCard(props) {

    const [modalIsOpen, setIsOpen] = useState(true);
    const [isLoading, setisLoading] = useState(false)
    const [isButtonEnabled, setisButtonEnabled] = useState(false)
    const [inputError, setinputError] = useState('')
    const { api_sendMobileOtpAttachment, api_verifyHolding, api_filterPropertyDetails } = CitizenApplyApiList()
    const { notify } = useContext(contextVar)
    const navigate = useNavigate()
    const [erroState, seterroState] = useState(false);
    const [erroMessage, seterroMessage] = useState(null);
    const [errorMsg, seterrorMsg] = useState('')
    const [searchType, setsearchType] = useState('')
    const [holdingList, setholdingList] = useState([])

    const validationSchema = yup.object({
        ptnNo: yup.string(),
        holdingNo: yup.string(),
    });

    const formik = useFormik({
        initialValues: {
            ptnNo: "",
            holdingNo: ""
        },
        onSubmit: (values) => {
            console.log('at submit form.....', values)
            // IF NO INPUT THEN GIVE ERROR
            // if (values.ptnNo.length < 14 && values.holdingNo < 15) {
            //     setinputError('Please enter PTN or holding no.')
            //     return
            // }

            // IF PTN LENGTH IS ZERO THEN SET HOLDING OR VICE VERSA
            // if (values.ptnNo.length == 0) {
            //     props?.setholdingNo(values.holdingNo)
            // } else {
            //     props?.setptnNo(values.ptnNo)
            // }
            // sendOtp(values)
        },
        validationSchema,
    });

    const handleChange = (e) => {
        let name = e.target.name
        let value = e.target.value

        // CLEAR OPPOSITE INPUT FIELD
        if (name == 'ptnNo') {
            formik.setFieldValue('holdingNo', '')
            props?.setholdingNo('')
        } else {
            formik.setFieldValue('ptnNo', '')
            props?.setptnNo('')
        }

        // INPUT RESTRICTION
        // { name == 'ptnNo' && formik.setFieldValue("ptnNo", allowCharacterNumberForwardInput(value, formik.values.ptnNo, 17)) }
        // { name == 'holdingNo' && formik.setFieldValue("holdingNo", allowCharacterNumberForwardInput(value, formik.values.holdingNo, 16)) }
        { name == 'ptnNo' && formik.setFieldValue("ptnNo", value) }
        { name == 'holdingNo' && formik.setFieldValue("holdingNo", value) }

        if ((name == 'ptnNo' && (value.length >= 14) || (name == 'holdingNo' && (value.length >= 14)))) {
            setisButtonEnabled(true)
        }
        else {
            setisButtonEnabled(false)
        }
    };

    // FUNCTION WHICH SENDS OTP TO REGISTERED MOBILE NO TO ATTACH HOLDING
    const sendOtp = (values) => {
        seterroState(false)
        setisLoading(true)
        setisButtonEnabled(false)
        // DUMMY OTP SEND MESSAGE
        // notify('Otp has been send to registered mobile no.', 'success')
        // props?.setptnCardStatus(false)
        // props?.setotpCardStatus(true)

        // return

        let requestBody
        if (values.ptnNo.length == 0) {
            requestBody = {
                holdingNo: values.holdingNo
            }
        } else {
            requestBody = {
                ptNo: values.ptnNo
            }
        }

        console.log('before send otp for attachment...', requestBody)
        // return
        axios
            .post(api_sendMobileOtpAttachment, requestBody, ApiHeader())
            .then((response) => {
                console.log('otp send for attachment response', response?.data)
                if (response?.data?.status) {
                    props?.setpropertyAttachedMobileNo(response?.data?.data?.mobileNo)
                    props?.setptnCardStatus(false)
                    props?.setotpCardStatus(true)
                } else {
                    activateBottomErrorCard(true, response?.data?.message)
                }
                setisLoading(false)

            })
            .catch((err) => {
                console.log("error otp send", err)
                notify('Something went wrong in attachment...', 'error')
                setisLoading(false)
                activateBottomErrorCard(true, 'Error occured. Please try again later.')
                setisButtonEnabled(true)
            });
    }

    const activateBottomErrorCard = (state, msg) => {
        seterroMessage(msg)
        seterroState(state)

    }

    // FUNCTION WHICH SENDS OTP TO REGISTERED MOBILE NO
    const funcVerifyHolding = (ptn, holding, ulb) => {

        if (formik.values.holdingNo == '' && formik.values.ptn == '') {
            activateBottomErrorCard(true, "Enter holding or pt no.")
            return
        }

        console.log("attach holding => ", ptn, holding, ulb)

        seterrorMsg('')
        console.log('otp send...')
        setisLoading(true)
        setisButtonEnabled(false)

        if (holding) {
            props?.setholdingNo(holding)
        } else {
            props?.setptnNo(ptn)
        }
        sendOtp(formik.values)
    }

    const COLUMNS = [
        {
            Header: "Ward No.",
            accessor: "ward_name",
        },
        {
            Header: "Owner's Name",
            accessor: "owner_name",
        },
        {
            Header: "PT No.",
            accessor: "pt_no",
            Cell: ({ cell }) => (<span>{nullToNA(cell.row.original.pt_no)}</span>)
        },
        {
            Header: "Holding No",
            accessor: "new_holding_no",
            Cell: ({ cell }) => (<span>{nullToNA(cell.row.original.new_holding_no)}</span>)

        },
        {
            Header: "Mobile No",
            accessor: "mobile_no",
            Cell: ({ cell }) => (<span>{nullToNA(cell.row.original.mobile_no)}</span>)

        },
        {
            Header: "Action",
            accessor: "id",
            Cell: ({ cell }) => (
                <button
                    onClick={() => funcVerifyHolding(cell?.row?.original?.pt_no, cell?.row?.original?.new_holding_no, cell?.row?.original?.ulb_id)}
                    className="bg-indigo-500 px-3 py-1 rounded-lg shadow-lg hover:shadow-xl hover:bg-indigo-700 text-white"
                >
                    Attach
                </button>
            ),
        },
    ];

    // Getting list of holding
    const getHoldingList = () => {

        let body

        Object.entries(formik?.values)?.map(([key, value], index) => {
            if (value != '') {
                body = {
                    filteredBy: key,
                    parameter: value,
                    isLegacy: false,
                    page: 1,
                    perPage: 100
                }
                setsearchType(key)
            }
        })

        setisLoading(true)

        console.log("gettting body => ", body)
        axios.post(api_filterPropertyDetails, body, ApiHeader())
            .then((res) => {
                console.log("holding list => ", res)
                if(res?.data?.status){
                    setholdingList(res?.data?.data?.data)
                    if (res?.data?.data?.data?.length == 0) {
                        activateBottomErrorCard(true, "No Data Found !!!")
                    }
                } else {
                    activateBottomErrorCard(true, res?.data?.message)
                }
            })
            .catch((err) => {
                console.log("error getting holding list => ", err)
            })
            .finally(() => setisLoading(false))
    }

    return (
        <>
            {
                isLoading && <BarLoader />
            }
            {erroState && <BottomErrorCard activateBottomErrorCard={activateBottomErrorCard} errorTitle={erroMessage} />}
            {/* <Modal
                isOpen={modalIsOpen}
                style={customStyles}
                contentLabel="Example Modal"
            > */}
            {/* <div class="w-screen h-screen"> */}
            <div class="w-full sm:w-full  mx-auto">
                <div class="container mx-auto">
                    <div class="mx-auto flex flex-wrap gap-4 justify-center items-center">
                        <div class="w-max bg-white rounded text-center shadow-xl relative py-10 px-8">
                            <div class="">
                                <button
                                    onClick={() => props?.closeModal(false)}
                                    type="button"
                                    class="absolute top-6 right-6 bg-transparent bg-gray-200 text-gray-900 rounded-lg text-sm p-1.5 ml-auto inline-flex items-center shadow-sm  hover:bg-red-200 hover:border-none"
                                >
                                    <svg class="w-5 h-5" fill="currentColor">
                                        <path
                                            fill-rule="evenodd"
                                            d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                                            clip-rule="evenodd"
                                        ></path>
                                    </svg>
                                </button>
                                <div class="text-2xl font-bold px-10">Enter Holding or PT no.
                                    {/* <button onClick={() => props?.setptnCardStatus(false)} type="button" class="float-right right-2.5 text-red-400 bg-transparent hover:bg-red-200 hover:text-gray-900 rounded-lg text-sm p-1.5 ml-auto inline-flex items-center " >
                                                <svg class="w-5 h-5" fill="currentColor" ><path fill-rule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clip-rule="evenodd"></path></svg>
                                            </button> */}
                                </div>
                                <div class="flex flex-col mt-4 text-center">
                                    <span className='text-center'>Enter 14 digit PTN no, or Holding no.<br />to attach a property</span>
                                    {/* <span class="font-bold">+91 ******876</span> */}
                                </div>

                                <div class="flex flex-col mt-4 text-center">
                                    <span className='text-center text-red-400'>{errorMsg}</span>
                                </div>
                                <div class="flex flex-row justify-center text-center mt-5">

                                    <form onSubmit={formik.handleSubmit} onChange={handleChange} >
                                        <div>
                                            <label className={`form-label inline-block mb-1 text-gray-600 text-sm font-semibold w-full text-left`}>PTN</label>
                                            <input type="text"  {...formik.getFieldProps('ptnNo')} className="form-control block w-full px-3 2xl:py-1.5 py-1 2xl:text-base text-sm font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none placeholder-gray-300 shadow-md"
                                            />
                                        </div>
                                        <div className='font-semibold text-indigo-500 mt-2'>Or</div>

                                        <div>
                                            <label className={`form-label inline-block mb-1 text-gray-600 text-sm font-semibold w-full text-left`}>Holding No.</label>
                                            <input type="text"  {...formik.getFieldProps('holdingNo')} className="form-control block w-full px-3 2xl:py-1.5 py-1 2xl:text-base text-sm font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none placeholder-gray-300 shadow-md"
                                            />
                                        </div>
                                        <div className='text-red-500 mt-10'>{inputError}</div>
                                        <div class="w-full text-center">

                                            {isButtonEnabled && <button type='button' onClick={getHoldingList} className="w-full  py-2.5 bg-indigo-600 text-white font-medium text-xs leading-tight  rounded shadow-md hover:bg-indigo-700 hover:shadow-lg focus:bg-indigo-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-indigo-800 active:shadow-lg transition duration-150 ease-in-out">{isLoading ? "Fetching..." : "Submit"}</button>}
                                            {!isButtonEnabled && <button type='button' className="w-full  py-2.5 bg-gray-400 text-white font-medium text-xs leading-tight  rounded shadow-md  hover:shadow-lg  focus:shadow-lg focus:outline-none focus:ring-0  transition duration-150 ease-in-out cursor-default ">Submit</button>}
                                        </div>
                                    </form>
                                </div>


                            </div>
                        </div>
                        {holdingList?.length > 0 &&

                            <div class="bg-white py-10 px-8 rounded text-center shadow-xl relative md:w-auto w-[97vw]">
                                {
                                    <ListTable dataList={holdingList} columns={COLUMNS} />
                                }
                            </div>}
                    </div>
                </div>
            </div>
            {/* </div> */}

            {/* </Modal> */}

        </>
    )
}

export default PtnNoCard